$text-xl: 23px;
$text-lg: 19px;
$text-md: 16px;
$text-sm: 12px;

$icon-lg: 20px;
$icon-md: 16px;

$edge-pad: 13px;

$light-1: #eee8f8;
$primary: #5514b4;
$dark-1: #441090;
$divider: #dcdcdc;
$halfopacity: rgba(0, 0, 0, 0.5);

:root {
    --primary: #{$primary};
    --halfopacity: #{$halfopacity};
}

$text-light: #fff;
$text-dark: #000;
$notif: #ff6767;

$bdr-rds-sm: 10px;
$bdr-rds-md: 20px;

input:not([type="checkbox"]),
button,
ul,
li,
a,
h1,
h2,
h3,
h4 {
    all: unset;
}
a,
button {
    cursor: pointer;
}
h1 {
    font-size: $text-xl;
}
h2 {
    font-size: $text-lg;
}
h3 {
    font-size: $text-md;
}
h4 {
    font-size: $text-sm;
}
